<script>
  import { cardSets } from "./Sets.svelte";
  import { fly } from "svelte/transition";

  import { beforeUpdate, afterUpdate } from "svelte";

  let hovering = false;

  function enter() {
    hovering = true;
  }

  function leave() {
    hovering = false;
  }
  export let card;
  let set;
  let code;
  let count;
  let index;
  let src;
  let src2;
  let name;
  let cost;
  let region;

  beforeUpdate(() => {
    set = card.set;
    code = card.code;
    count = card.count;

    index = cardSets[set].findIndex(obj => obj.cardCode == code);
    src = cardSets[set][index].assets[0].fullAbsolutePath;
    // src = "img/cards/" + code + "-full.png";
    src2 = cardSets[set][index].assets[0].gameAbsolutePath;
    // src2 = "img/cards/" + code + ".png";

    name = cardSets[set][index].name;
    cost = cardSets[set][index].cost;
    region = cardSets[set][index].regionRef;
  });
</script>

