    color: white;
  }
</style>

<hover on:mouseenter={enter} on:mouseleave={leave}>
  <cardblock style="background-image: url({src})">
    <region class={region}>
      <div class="cardcost">
        <span>{cost}</span>
      </div>
      <div class="cardname">{name}</div>
      <div class="cardnums">{count}</div>
    </region>
  </cardblock>
  {#if hovering}
    <img src={src2} class="hoverview" alt={name} transition:fly="{{y: 200,duration: 500 }}" />
  {/if}
</hover>
